body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
  color: #050505;
}

/* CONTAINER */
.container{
  padding: 0px 60px;
}

/* NAVBAR */
.navbar{
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo{
  margin: 0;
  font-size: 20px;
}
.logo:hover{
  cursor: pointer;
}

.navlinks{
  font-size: 15px;
  font-weight: 800;
  padding-left: 5vw;
  text-decoration: none;
  color: #050505;
}
.navlinks:hover{
  color: rgb(8, 63, 165);;
}

/* MEDIA QUERY */
@media screen and (max-width: 768px)  {
  .navbar{
    display: flex;
    flex-direction: column;
  }
  .navlinks{
    font-size: 13px;
  }
  .logo{
    font-size: 16px;
  }
  .container{
    padding: 0px 10px;
  }
 }

 /* HOME STYLING */
.hi{
  color: #000000;
  font-weight: 800;
  letter-spacing: 1px;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  font-size: 7vw;
}
.text{
  font-size: 7vw;
  font-weight: 700;
  color: #050505;
}
.homehead{
  /* background-color: blue; */
  display: inline-block;
}
.hi:hover{
  color: rgb(8, 63, 165);;
}
.text:hover{
  color: rgb(8, 63, 165);
}

/* ABOUT CONTAINER */
.abouthead {
  font-size: 4vw;
}
.abouttext{
  font-size: 2vw;
}